@property --a {
  syntax: "<angle>";
  inherits: false;
  initial-value: 0deg;
}
@property --i {
  syntax: "<number>";
  inherits: false;
  initial-value: 0;
}

body {
  background: linear-gradient(to bottom, #2c3e50, #3498db);
}

.image-container {
  display: flex;
  gap: 50px;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #000;
 
}

.unique-lighthouse, .unique-old-city {
  width: 300px; /* Adjust as per your image dimensions */
  height: 400px; /* Adjust as per your image dimensions */
  border-radius: 30px;
  border: 5px solid #ccc;
  padding: 10px;
  mask: conic-gradient(#000 0 0) content-box,
    linear-gradient(
        calc(mod(var(--a), 180deg) * var(--i) + var(--_a, 45deg)),
        #0000 30%,
        #000 40% 60%,
        #0000 70%
      )
      subtract,
    conic-gradient(#000 0 0) padding-box;
  transition: --i 0.5s, --a 0s 0.5s;
  cursor: pointer;
  margin-top: 120px;
}

.unique-old-city {
  --_a: -45deg;
}

.unique-lighthouse:hover, .unique-old-city:hover {
  --i: 1;
  --a: 15turn;
  transition: --i 0s, --a 30s linear;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px; /* Optional for smoother corners */
}
